import React, { Component } from "react";
import axios from "axios";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { connect } from "react-redux";
import validationTerimaSupplier from "../../Validations/validationTerimaSupplier";
import { createNumberMask, createTextMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",00",
  locale: "id-ID",
});

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;

class FormTerimaSupplier extends Component {
  constructor(props) {
    super(props);
    this.onclick1 = this.onclick1.bind(this);
    this.state = {
      kodeBarang: [],
      DataSupplier: [],
      kode_barang: "",
      keterangan: "",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
    };
  }

  componentDidMount() {
    axios
      .get(server + "subcategorys")
      .then((res) =>
        this.setState({
          kodeBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  inputChange = (input) => (e) => {
    if (
      !(
        (e.keyCode > 95 && e.keyCode < 106) ||
        (e.keyCode > 47 && e.keyCode < 58) ||
        e.keyCode === 8
      )
    ) {
      e.preventDefault();
    } else {
      this.setState({
        [input]: e.target.value,
      });
    }
  };
  inputKeterangan = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  inputHarga = (input) => (e) => {
    if (input === null) {
      return false;
    } else {
      let berat = parseFloat(this.state.berat);
      let murni = parseFloat(berat) * parseFloat(e.target.value / 100);
      console.log(berat, murni);
      this.setState({
        [input]: e.target.value,
        murni: parseFloat(murni),
      });
    }
  };

  submit = () => {
    var data = {
      kode_barang: this.state.kode_barang,
      keterangan: this.state.keterangan,
      kadar: this.state.kadar,
      P: this.state.P,
      K: this.state.K,
      bungkus: this.state.bungkus,
      berat: this.state.berat,
      harga: this.state.harga,
      murni: this.state.murni,
      ongkos: this.state.ongkos,
    };
    var array = this.state.DataSupplier;
    array.push(data);
    console.log(JSON.stringify(this.state.DataSupplier));
    this.setState({ status: "sukses" });
  };

  onclick1() {
    this.props.change("murni", this.props.murni);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 form-group">
            <label htmlFor="">Kode Barang</label>
            <Field
              name="kodeBarang"
              component="select"
              className="form-control"
            >
              {this.state.kodeBarang === [] ? (
                <option>TIDAK ADA DATA</option>
              ) : (
                this.state.kodeBarang.map((kode) => (
                  <option value={kode.kodeJenis}>{kode.namaJenis}</option>
                ))
              )}
            </Field>
          </div>
          <div
            className="col-md-8 form-group"
            style={
              this.props.from === "terima" ? null : { visibility: "hidden" }
            }
          >
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="kadar"
              label="Kadar (%)"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field name="P" label="P" type="number" component={renderField} />
          </div>
          <div className="col-md-3 form-group">
            <Field name="k" label="K" type="number" component={renderField} />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="bungkus"
              label="Bungkus (Gr)"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="berat"
              label="Berat (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.props.change("murni", this.props.murni)}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="harga"
              label="Harga (%)"
              type="number"
              component={renderField}
              onBlur={() => this.props.change("murni", this.props.murni)}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="murni"
              label="Murni (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Ongkos (Rp)</label>
            <Field
              // name="ongkos"
              // label="Ongkos (Rp)"
              // type="number"
              // component={renderField}
              name="amount"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-12 form-group mt-3">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                disabled={this.props.submitting}
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTerimaSupplier = reduxForm({
  form: "TerimaSupplier",
  validate: validationTerimaSupplier,
  enableReinitialize: true,
})(FormTerimaSupplier);
const selector = formValueSelector("TerimaSupplier"); // <-- same as form name

export default connect((state) => {
  const { berat, harga } = selector(state, "berat", "harga");
  return {
    murni: (parseFloat(berat) * (parseFloat(harga) / 100)).toFixed(3),
  };
})(FormTerimaSupplier);
