import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import axios from "axios";
import FormParameter from "./Component/FormParameter";
import { getListParameter } from "../actions/parameterTransaksiAction";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { reset } from "redux-form";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "parameteruang", {
          data: {
            kodeParameter: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            title: "Berhasil!",
            text: "Data Berhasil Dihapus!",
            icon: "success",
            confirmButtonText: "OK",
          })
        )
        .catch((err) =>
          Swal.fire({
            title: "Gagal!",
            text: "GAGAL DELETE",
            icon: "warning",
            confirmButtonText: "OK",
          })
        )
        .then(() => dispatch(getListParameter()));
    }
  });
}
const maptoState = (state) => {
  return {
    kodeParameter: state.provinsi.parameterTransaksi,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

const server = process.env.REACT_APP_BACKEND_URL;
class ParameterTransaksi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ParameterTransaksi: [],
      response: "",
      columns: [
        {
          dataField: "kodeParameter",
          text: "Kode ParameterTransaksi",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeParameter)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
              </div>
            );
          },
          headerStyle: () => {
            return { width: "10%" };
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListParameter());
  }

  handleSubmit(data) {
    axios
      .post(server + "parameteruang", {
        kodeParameter: data.kodeParameter,
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getListParameter()))
      .then(() => this.props.dispatch(reset("Parameter")));
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Data ParameterTransaksi
                </h4>
                <form action="#">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          type="button"
                          className="btn btn-info"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          Tambah Data Parameter
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                {this.props.kodeParameter ? (
                  <ToolkitProvider
                    keyField="id"
                    data={this.props.kodeParameter}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <div className="text-right mt-5 mb-3">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Tuliskan Kode / Nama"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                        />
                        <ExportCSVButton {...props.csvProps}>
                          <button className="btn btn-primary">
                            Export CSV
                          </button>
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : null}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Provinsi
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormParameter onSubmit={(data) => this.handleSubmit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(ParameterTransaksi);
