import React, { Component } from "react";
import Pdf from "react-to-pdf";

const ref = React.createRef();

const PDF = (props) => {
  return (
    <>
      <div className="PDF" ref={ref}>
        <h3 className="text-center">{props.title}</h3>
        <table width="100%" style={{ background: "#000" }}>
          <tr>
            <td>
              <td>
                <p>No Terima</p>
              </td>
              <td>
                <p>:</p>
              </td>
              <td>
                <p>AS0192093019</p>
              </td>
            </td>
            <td>
              <td>
                <p>No Terima</p>
              </td>
              <td>
                <p>:</p>
              </td>
              <td>
                <p>AS0192093019</p>
              </td>
            </td>
          </tr>
          <tr>
            <td>
              <td>
                <p>No Terima</p>
              </td>
              <td>
                <p>:</p>
              </td>
              <td>
                <p>AS0192093019</p>
              </td>
            </td>
            <td>
              <td>
                <p>No Terima</p>
              </td>
              <td>
                <p>:</p>
              </td>
              <td>
                <p>AS0192093019</p>
              </td>
            </td>
          </tr>
        </table>
        <p>{props.content}</p>
      </div>
      <Pdf targetRef={ref} filename="post.pdf">
        {({ toPdf }) => <button onClick={toPdf}>Capture as PDF</button>}
      </Pdf>
    </>
  );
};

export default PDF;
