import React, { Component } from "react";
import Axios from "axios";
import FormTerimaSupplier from "./Component/FormTerimaSupplier";

const column = [
  {
    dataField: "kode_barang",
    text: "kode_barang",
    sort: true,
  },
  {
    dataField: "keterangan",
    text: "keterangan",
    sort: true,
  },
  {
    dataField: "kadar",
    text: "kadar",
    sort: true,
  },
  {
    dataField: "P",
    text: "P",
    sort: true,
  },
  {
    dataField: "K",
    text: "K",
    sort: true,
  },
  {
    dataField: "bungkus",
    text: "bungkus",
    sort: true,
  },
  {
    dataField: "berat",
    text: "berat",
    sort: true,
  },
  {
    dataField: "harga",
    text: "harga",
    sort: true,
  },
  {
    dataField: "murni",
    text: "murni",
    sort: true,
  },
  {
    dataField: "ongkos",
    text: "ongkos",
    sort: true,
  },
];
const defaultSorted = [
  {
    dataField: "city",
    order: "asc",
  },
];
const server = process.env.REACT_APP_BACKEND_URL;

class ReturnSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      DataSupplier: [],
      kode_barang: "",
      keterangan: "",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
    };
  }

  componentDidMount() {
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => alert(err));
  }

  handleSubmit(data) {
    console.log(data);
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Supplier</label>
                      <select
                        name="supplier"
                        id="supplier"
                        className="form-control"
                      >
                        {this.state.listSupplier.map((list) => (
                          <option value={list.kodeSupplier}>
                            {list.namaSupplier}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal</label>
                      <input
                        type="date"
                        id="tanggal"
                        name="tanggal"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">No Return</label>
                      <input
                        type="text"
                        id="no_return"
                        name="no_return"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-4 col-lg-4 mt-3">
                      <label htmlFor="">No Bon</label>
                      <input
                        type="text"
                        id="bon_return"
                        name="bon_return"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-12 col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          Tambah Data
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Return Supplier
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormTerimaSupplier
                  from="return"
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ReturnSupplier;
