import React, { Component } from "react";
import AreaProvinsiKota from "./Component/AreaProvinsiKota";
import Table from "./Component/Table";
import activelogo from "../active.png";
import nonactive from "../nonactive.png";
import { connect } from "react-redux";
import notrespond from "../500.svg";
import { editarea, getareaList } from "../actions/areaAction";
import axios from "axios";
import Swal from "sweetalert2";
import FormAreaEdit from "./Component/FormAreaEdit";
import { reset } from "redux-form";

function status(cell, row) {
  if (row.statusAktif === "AKTIF") {
    return (
      <div>
        <img src={activelogo} style={{ width: 50, height: 50 }} alt="ACTIVE" />
      </div>
    );
  } else {
    return (
      <div>
        <img
          src={nonactive}
          alt="NON ACTIVE"
          style={{ width: 50, height: 50 }}
        />
      </div>
    );
  }
}
const server = process.env.REACT_APP_BACKEND_URL;
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "area", {
          data: {
            kodeArea: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Data Gagal Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .then(() => dispatch(getareaList()));
    }
  });
}

const defaultSorted = [
  {
    dataField: "kodeArea",
    order: "asc",
  },
];
const maptoState = (state) => {
  return {
    areaList: state.provinsi.area,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

class Area extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Area: [],
      AreaArray: [],
      response: "",
      columns: [
        {
          dataField: "kodeArea",
          text: "Kode Area",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "namaArea",
          text: "Nama Area",
          sort: true,
        },
        {
          dataField: "kodeKota",
          text: "Kode Kota",
          sort: true,
        },
        {
          dataField: "statusAktif",
          text: "Status",
          sort: true,
          formatter: status,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeArea)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() =>
                    this.props.dispatch(
                      editarea(
                        row.kodeArea,
                        row.namaArea,
                        row.kodeKota,
                        row.statusAktif
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-1"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getareaList());
  }

  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getareaList());
  };

  handleSubmit(data) {
    axios
      .post(server + "area", {
        statusAktif: data.status ? data.status : "AKTIF",
        namaArea: data.NamaArea,
        kodeArea: data.KodeArea,
        kodeKota: data.kodeKota,
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getareaList()))
      .then(() => this.props.dispatch(reset("Provinsi")));
  }

  handleEdit(data) {
    console.log(data);
    axios
      .put(server + "area", {
        statusAktif: data.status ? data.status : "AKTIF",
        namaArea: data.NamaArea,
        kodeArea: data.KodeArea,
        kodeKota: data.kodeKota,
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Diubah!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal1").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Diubah!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getareaList()))
      .then(() => this.props.dispatch(reset("Provinsi")));
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Data Area
                </h4>
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                    >
                      Tambah Data Area
                    </button>
                  </div>
                </div>
                {this.props.areaList ? (
                  <Table
                    data={this.props.areaList}
                    columns={this.state.columns}
                    defaultSorted={defaultSorted}
                  />
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img
                          src={notrespond}
                          style={{ width: "30%" }}
                          alt="NOT RESPOND"
                        />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <div class="d-flex justify-content-center">
                        <div class="spinner-border" role="status">
                          <span class="sr-only">Loading...</span>
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Area
                </h4>
                <button
                  id="dismiss_modal"
                  name="dismiss_modal"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <AreaProvinsiKota
                  nama="Nama Area"
                  kode="Kode Area"
                  kode_ext="Nama Kota"
                  updateStatus={this.updateStatus}
                  onSubmit={(data) => this.handleSubmit(data)}
                  from="area"
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Area
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormAreaEdit
                  nama="Nama Area"
                  kode="Kode Area"
                  kode_ext="Nama Kota"
                  onSubmit={(data) => this.handleEdit(data)}
                  from="area"
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Area);
