import React, { Component } from "react";

class FormMutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kode_asal: "",
      nama_asal: "",
      kadar_asal: "",
      pkg_asal: "",
      kotor_asal: "",
      harga_asal: "",
      murni_asal: "",
      kode_tujuan: "",
      nama_tujuan: "",
      kadar_tujuan: "",
      pkg_tujuan: "",
      kotor_tujuan: "",
      harga_tujuan: "",
      murni_tujuan: "",
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };
  render() {
    return (
      <div className="row">
        <div className="col-md-4">
          <label htmlFor="">Kode Asal</label>
          <select
            name="kode_asal"
            id="kode_asal"
            className="form-control"
            onChange={this.inputChange("kode_asal")}
          >
            <option value="BARANG 1">BARANG 1</option>
            <option value="BARANG 2">BARANG 2</option>
          </select>
        </div>
        <div className="col-md-4">
          <label htmlFor="">Kode Asal</label>
          <input
            name="nama_asal"
            id="nama_asal"
            className="form-control"
            onChange={this.inputChange("nama_asal")}
          />
        </div>
        <div className="col-md-4 col-lg-4"></div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Kadar (%)</label>
          <input
            name="kadar"
            id="kadar"
            className="form-control"
            onChange={this.inputChange("kadar_asal")}
          />
        </div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Pkg (Gr)</label>
          <input
            name="package"
            id="package"
            className="form-control"
            onChange={this.inputChange("pkg_asal")}
          />
        </div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Kotor (Gr)</label>
          <input
            name="kotor"
            id="kotor"
            className="form-control"
            onChange={this.inputChange("kotor_asal")}
          />
        </div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Harga (%)</label>
          <input
            name="harga"
            id="harga"
            className="form-control"
            onChange={this.inputChange("harga_asal")}
          />
        </div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Murni (Gr)</label>
          <input
            name="murni"
            id="murni"
            className="form-control"
            onChange={this.inputChange("murni_asal")}
          />
        </div>
        <div className="col-lg-12 col-md-12 text-center mt-2 mb-2">
          <div style={{ transform: "rotate(90deg)", fontSize: 24 }}>
            <i className="fa fa-exchange-alt"></i>
          </div>
        </div>
        <div className="col-md-4">
          <label htmlFor="">Kode tujuan</label>
          <select
            name="kode_tujuan"
            id="kode_tujuan"
            className="form-control"
            onChange={this.inputChange("kode_tujuan")}
          >
            <option value="BARANG 1">BARANG 1</option>
            <option value="BARANG 2">BARANG 2</option>
          </select>
        </div>
        <div className="col-md-4">
          <label htmlFor="">Kode tujuan</label>
          <input
            name="nama_tujuan"
            id="nama_tujuan"
            className="form-control"
            onChange={this.inputChange("nama_tujuan")}
          />
        </div>
        <div className="col-md-4 col-lg-4"></div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Kadar (%)</label>
          <input
            name="kadar"
            id="kadar"
            className="form-control"
            onChange={this.inputChange("kadar_tujuan")}
          />
        </div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Pkg (Gr)</label>
          <input
            name="package"
            id="package"
            className="form-control"
            onChange={this.inputChange("pkg_tujuan")}
          />
        </div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Kotor (Gr)</label>
          <input
            name="kotor"
            id="kotor"
            className="form-control"
            onChange={this.inputChange("kotor_tujuan")}
          />
        </div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Harga (%)</label>
          <input
            name="harga"
            id="harga"
            className="form-control"
            onChange={this.inputChange("harga_tujuan")}
          />
        </div>
        <div className="col-md-3 mt-1">
          <label htmlFor="">Murni (Gr)</label>
          <input
            name="murni"
            id="murni"
            className="form-control"
            onChange={this.inputChange("murni_tujuan")}
          />
        </div>
        <div className="col-lg-12 col-md-12">
          <div className="text-right">
            <button className="btn btn-primary" onClick={this.submit}>
              Simpan
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default FormMutasiBarang;
