import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, initialize, reduxForm } from "redux-form";
import validaitonRekening from "../../Validations/validaitonRekening";

const renderField = ({
  input,
  label,
  type,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p style={{ color: "#ff4f70" }}>{error}</p>) ||
        (warning && <p style={{ color: "#ff4f70" }}>{warning}</p>))}
  </div>
);

const maptoState = (state) => {
  return {
    initialValues: {
      noRekening: state.provinsi.editDataRekening.noRekening,
      atasNama: state.provinsi.editDataRekening.atasNama,
      namaBank: state.provinsi.editDataRekening.namaBank,
      mataUang: state.provinsi.editDataRekening.mataUang,
    },
  };
};
class FormRekeningEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
    };
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-4 col-md-4">
              <Field
                name="noRekening"
                type="number"
                component={renderField}
                label="Nomor Rekening"
                readOnly={true}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="namaBank"
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Nama Bank"
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="atasNama"
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Atas Nama"
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <label htmlFor="">Mata Uang</label>
              <Field
                name="mataUang"
                component="select"
                className="form-control"
                label="Mata Uang"
              >
                <option value="RUPIAH">RUPIAH</option>
                <option value="USD">USD</option>
              </Field>
            </div>
            <div className="col-md-12 col-lg-12 mt-5">
              <div className="text-right">
                <button
                  type="submit"
                  className="btn btn-primary"
                  disabled={this.props.submitting}
                >
                  Simpan
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FormRekeningEdit = reduxForm({
  form: "FormRekeningEdit",
  validate: validaitonRekening,
  enableReinitialize: true,
})(FormRekeningEdit);
export default connect(maptoState, null)(FormRekeningEdit);
