import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import FormMutasiBarang from "./Component/FormMutasiBarang";
import axios from "axios";

const columns = [
  {
    dataField: "No",
    text: "No",
  },
  {
    dataField: "KodeBarang",
    text: "Kode Barang",
  },
  {
    dataField: "keterangan",
    text: "Keterangan",
  },
  {
    dataField: "kat_harga",
    text: "Kat Harga",
  },
  {
    dataField: "Kadar",
    text: "Kadar",
  },
  {
    dataField: "pkg_gr",
    text: "Pkg (Gr)",
  },
  {
    dataField: "Bruto",
    text: "Bruto (Gr)",
  },
  {
    dataField: "harga",
    text: "Harga",
  },
  {
    dataField: "netto",
    text: "Netto (Gr)",
  },
  {
    dataField: "netto",
    text: "Netto (Gr)",
  },
  {
    dataField: "ongkos",
    text: "Ongkos (Rp)",
  },
  {
    dataField: "Qty",
    text: "Qty",
  },
];

const products = [
  {
    kode_salesman: "001",
    nama_salesman: "BANDUNG",
  },
  {
    kode_salesman: "002",
    nama_salesman: "BOGOR",
  },
];

const server = process.env.REACT_APP_BACKEND_URL;
class MutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lokasi: [],
    };
  }

  componentDidMount() {
    axios.get(server + "storages").then((res) =>
      this.setState({
        lokasi: res.data,
      })
    );
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Mutasi Barang
                </h5>
                <div className="row">
                  <div className="col-md-4 form-group">
                    <label>Nomor Mutasi</label>
                    <input
                      type="text"
                      id="no_mutasi"
                      name="no_mutasi"
                      className="form-control"
                      readOnly
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Tanggal</label>
                    <input
                      type="date"
                      id="tanggal"
                      name="tannggal"
                      className="form-control"
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Bon Mutasi</label>
                    <input
                      type="text"
                      id="bon_mutasi"
                      name="bon_mutasi"
                      className="form-control"
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Kategori</label>
                    <select
                      id="kategori"
                      name="kategori"
                      className="form-control"
                    >
                      <option value="">AYU GOLD</option>
                      <option value="">AYU GOLD</option>
                    </select>
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Keterangan</label>
                    <input
                      type="text"
                      id="keterangan"
                      name="keterangan"
                      className="form-control"
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Lokasi</label>
                    <select
                      type="text"
                      id="keterangan"
                      name="keterangan"
                      className="form-control"
                    >
                      {this.state.lokasi.map((lokasi) => (
                        <option value={lokasi.kodeLokasi}>
                          {lokasi.namaLokasi}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-left">
                      <button className="btn btn-primary" data-toggle="modal">
                        SIMPAN
                      </button>
                      <button
                        className="btn btn-primary ml-2"
                        data-toggle="modal"
                      >
                        BATAL
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-center">
                      <button className="btn btn-primary" data-toggle="modal">
                        BARCODE (+)
                      </button>
                      <button
                        className="btn btn-primary ml-2"
                        data-toggle="modal"
                      >
                        BARCODE (-)
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        data-toggle="modal"
                        data-target="#primary-header-modal"
                      >
                        TAMBAH BARANG
                      </button>
                    </div>
                  </div>
                  <BootstrapTable
                    keyField="id"
                    data={products}
                    columns={columns}
                    bordered={true}
                    headerClasses="header-table"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Mutasi Barang
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormMutasiBarang />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default MutasiBarang;
