import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import axios from "axios";
import Swal from "sweetalert2";

const renderField = ({
  input,
  label,
  type,
  limit,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input {...input} type={type} className="form-control" maxLength={limit} />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const validationkodeParameter = (value) => {
  const errors = {};
  if (!value.kodeParameter) {
    errors.kodeParameter = "Tidak Boleh kosong";
  }
  return errors;
};
class FormParameter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      kategori: "",
    };
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3 col-md-3"></div>
          <div className="col-md-6 text-center">
            <Field
              name="kodeParameter"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Parameter"
            />
          </div>
          <div className="col-lg-3 col-md-3"></div>
          <div className="col-md-12 col-lg-12">
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info"
                disabled={this.props.submitting}
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormParameter = reduxForm({
  form: "Parameter",
  validationkodeParameter,
  enableReinitialize: true,
})(FormParameter);
export default connect()(FormParameter);
