import React, { Component } from "react";
import axios from "axios";
import FormTerimaSupplier from "./Component/FormTerimaSupplier";

const defaultSorted = [
  {
    dataField: "city",
    order: "asc",
  },
];

const server = process.env.REACT_APP_BACKEND_URL;

class TolakanReturnSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      supplierList: [],
      DataSupplier: [],
      kode_barang: "",
      keterangan: "",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
    };
  }

  componentDidMount() {
    axios
      .get(server + "suppliers")
      .then((res) =>
        this.setState({
          supplierList: res.data,
        })
      )
      .catch((err) => alert("ERROR GET DATA"));
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Supplier</label>
                      <select
                        name="supplier"
                        id="supplier"
                        className="form-control"
                      >
                        {this.state.supplierList == [] ? (
                          <option>DATA KOSONG</option>
                        ) : (
                          this.state.supplierList.map((list) => (
                            <option value={list.kodeSupplier}>
                              {list.namaSupplier}
                            </option>
                          ))
                        )}
                      </select>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal</label>
                      <input
                        type="date"
                        id="tanggal"
                        name="tanggal"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-4 col-lg-4 ">
                      <label htmlFor="">No Bon</label>
                      <input
                        type="text"
                        id="bon_return"
                        name="bon_return"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-12 col-lg-12 mt-3">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          Tambah Data
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Tolakan Return Supplier
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormTerimaSupplier form="tolakan" />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default TolakanReturnSupplier;
