import React, { Component } from 'react';
import './App.css';
import logo from './logo_test.svg'
import { Route, Switch } from "react-router-dom";
import Dashboard from "./Dashboard";
import Provinsi from "./DataMaster/Provinsi";
import Kota from "./DataMaster/Kota";
import Area from "./DataMaster/Area";
import DaftarRekening from "./DataMaster/DaftarRekening";
import Lokasi from "./DataMaster/Lokasi";
import Sidebar from "./Sidebar";
import Salesman from "./DataMaster/Salesman";
import Supplier from "./DataMaster/Supplier";
import Customer from "./DataMaster/Customer";
import Koderosok from "./DataMaster/Koderosok";
import SaldoAwal from "./DataMaster/SaldoAwal";
import Kadar from "./DataMaster/Kadar";
import KlasifikasiBarang from "./DataMaster/KlasifikasiBarang";
import ParameterTransaksi from "./DataMaster/ParameterTransaksi";
import Header from "./Header";
import PindahBarang from './Stocking/PindahBarang';
import MutasiBarang from './Stocking/MutasiBarang';
import MutasiCiok from './Stocking/MutasiCiok';
import KirimMasak from './Stocking/KirimMasak';
import TerimaMasak from './Stocking/TerimaMasak';
import KirimBahan from './Stocking/KirimBahan';
import TerimaSupplier from './Transaksi/TerimaSupplier';
import ReturnSupplier from './Transaksi/ReturnSupplier';
import TolakanReturnSupplier from './Transaksi/TolakanReturnSupplier';
import PengambilanBarcod from './Transaksi/PengambilanBarcode';
import PembayaranSupplier from './FICO/PembayaranSupplier';
import { connect } from 'react-redux';
import { setDefault, setLogin } from './actions/titleAction';
import Kategori from './DataMaster/Kategori';
import KodeBarang from './DataMaster/KodeBarang';
import Kelompok from './DataMaster/Kelompok';
import ClosePengambilanBarcode from './Transaksi/ClosePengambilanBarcode';



const maptoState = (state) => {
  return {
    login: state.title.Login,
  }
}
class App extends Component {


  componentDidMount() {
    this.props.dispatch(setDefault())
  }
  render() {
    var link = window.location.pathname;
    switch (link) {
      case "/ayugold.com/provinsi":
        link = "Data Provinsi";
        break;
      case "/ayugold.com/dashboard":
        link = "Dashboard"
        break;
      case "/ayugold.com/kota":
        link = "Data Kota"
        break;
      case "/ayugold.com/area":
        link = "Data Area"
        break;
      case "/ayugold.com/rekening":
        link = "Data Rekening"
        break;
      case "/ayugold.com/lokasi":
        link = "Data Lokasi"
        break;
      case "/ayugold.com/salesman":
        link = "Data Salesman"
        break;
      case "/ayugold.com/supplier":
        link = "Data Supplier"
        break;
      case "/ayugold.com/customer":
        link = "Data Customer"
        break;
      case "/ayugold.com/koderosok":
        link = "Data Kode Rosok dan Ciok"
        break;
      case "/ayugold.com/saldoawal":
        link = "Data Saldo Awal"
        break;
      case "/ayugold.com/kadar":
        link = "Data Kadar"
        break;
      case "/ayugold.com/KlasifikasiBarang":
        link = "Klasifikasi Barang"
        break;
      case "/ayugold.com/parameter":
        link = "Parameter Transaksi Keuangan"
        break;
      case "/ayugold.com/pindahbarang":
        link = "Pindah Barang"
        break;
      case "/ayugold.com/mutasibarang":
        link = "Mutasi Barang"
        break;
      case "/ayugold.com/mutasiciok":
        link = "Mutasi Ciok"
        break;
      case "/ayugold.com/kirimmasak":
        link = "Kirim Masak"
        break;
      case "/ayugold.com/terimamasak":
        link = "Terima Masak"
        break;
      case "/ayugold.com/kirimbahan":
        link = "Kirim Bahan"
        break;
      case "/ayugold.com/terimasupplier":
        link = "Penerimaan Supplier"
        break;
      case "/ayugold.com/returnsupplier":
        link = "Return Supplier"
        break;
      case "/ayugold.com/tolakanreturnsupplier":
        link = "Tolakan Return Supplier"
        break;
      case "/ayugold.com/ambilbarcode":
        link = "Transaksi Pengambilan Barcode"
        break;
      default:
        link = "AYU GOLD"
    }
    if (this.props.login === true) {
      return (
        <div id="main-wrapper" data-theme="light" data-layout="vertical" data-navbarbg="skin6" data-sidebartype="mini-sidebar"
          data-sidebar-position="fixed" data-header-position="fixed" data-boxed-layout="full">
          <Header />
          <Sidebar />

          <div className="page-wrapper">
            <Switch>
              <Route exact path="/ayugold.com/" component={Dashboard} />
              <Route exact path="/ayugold.com/dashboard" component={Dashboard} />
              <Route path="/ayugold.com/provinsi" component={Provinsi} />
              <Route path="/ayugold.com/kota" component={Kota} />
              <Route path="/ayugold.com/area" component={Area} />
              <Route path="/ayugold.com/rekening" component={DaftarRekening} />
              <Route path="/ayugold.com/lokasi" component={Lokasi} />
              <Route path="/ayugold.com/salesman" component={Salesman} />
              <Route path="/ayugold.com/supplier" component={Supplier} />
              <Route path="/ayugold.com/customer" component={Customer} />
              <Route path="/ayugold.com/koderosok" component={Koderosok} />
              <Route path="/ayugold.com/saldoawal" component={SaldoAwal} />
              <Route path="/ayugold.com/kadar" component={Kadar} />
              <Route path="/ayugold.com/KlasifikasiBarang" exact component={KlasifikasiBarang} />
              <Route path="/ayugold.com/kodebarang" exact component={KodeBarang} />
              <Route path="/ayugold.com/parameter" component={ParameterTransaksi} />
              <Route path="/ayugold.com/pindahbarang" component={PindahBarang} />
              <Route path="/ayugold.com/mutasibarang" component={MutasiBarang} />
              <Route path="/ayugold.com/mutasiciok" component={MutasiCiok} />
              <Route path="/ayugold.com/kirimmasak" component={KirimMasak} />
              <Route path="/ayugold.com/terimamasak" component={TerimaMasak} />
              <Route path="/ayugold.com/kirimbahan" component={KirimBahan} />
              <Route path="/ayugold.com/terimasupplier" component={TerimaSupplier} />
              <Route path="/ayugold.com/returnsupplier" component={ReturnSupplier} />
              <Route path="/ayugold.com/tolakanreturnsupplier" component={TolakanReturnSupplier} />
              <Route path="/ayugold.com/ambilbarcode" component={PengambilanBarcod} />
              <Route path="/ayugold.com/bayarsupplier" component={PembayaranSupplier} />
              <Route path="/ayugold.com/kategori" exact component={Kategori} />
              <Route path="/ayugold.com/kelompok" exact component={Kelompok} />
              <Route path="/ayugold.com/closePengambilanBarang" exact component={ClosePengambilanBarcode} />
            </Switch>
          </div>
        </div>
      );
    } else {
      return (
        <div className="main-wrapper">
          <div className="auth-wrapper d-flex no-block justify-content-center align-items-center position-relative">
            <div className="auth-box row" style={{ width: "80%" }}>
              <div className="col-lg-7 col-md-5 modal-bg-img" style={{ backgroundImage: 'url(./logo_test.svg)' }}>
                <img src={logo} alt="Logo Perusahaan" />
              </div>
              <div className="col-lg-5 col-md-7 bg-white">
                <div className="p-3">
                  <h2 className="mt-3 text-center">Selamat Datang</h2>
                  <p className="text-center">Silahkan masukan Email dan password untuk Masuk Kedalam sistem.</p>
                  <form className="mt-4">
                    <div className="row">
                      <div className="col-lg-12">
                        <div className="form-group">
                          <label className="text-dark" htmlFor="uname">Email</label>
                          <input className="form-control" id="uname" type="text" placeholder="Masukan Email" />
                        </div>
                      </div>
                      <div className="col-lg-12">
                        <div className="form-group">
                          <label className="text-dark" htmlFor="pwd">Password</label>
                          <input className="form-control" type="password" id="pwd" placeholder="Masukan Password" />
                        </div>
                      </div>
                      <div className="col-lg-12 text-center">
                        <button type="submit" className="btn btn-block btn-dark" onClick={() => setLogin()}>Masuk</button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default connect(maptoState, null)(App);
