import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import FormSalesman from "./Component/FormSalesman";
import axios from "axios";
import activelogo from "../active.png";
import nonactive from "../nonactive.png";
import { editsalesmanList, getsalesmanList } from "../actions/salesmanAction";
import { connect } from "react-redux";
import notrespond from "../500.svg";
import Swal from "sweetalert2";
import FormSalesmanEdit from "./Component/FormSalesmanEdit";
import { reset } from "redux-form";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function status(cell, row) {
  if (row.statusAktif === "AKTIF") {
    return (
      <div>
        <img src={activelogo} style={{ width: 50, height: 50 }} />
      </div>
    );
  } else {
    return (
      <div>
        <img src={nonactive} style={{ width: 50, height: 50 }} />
      </div>
    );
  }
}
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "storage", {
          data: {
            kodeLokasi: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Data Gagal Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .then(() => dispatch(getsalesmanList()));
    }
  });
}

const expandRow = {
  renderer: (row) => (
    <div>
      <p>{`Kode Ext: ${row.kodeExt}`}</p>
    </div>
  ),
};
const maptoState = (state) => {
  return {
    salemanList: state.provinsi.salesman,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

const server = process.env.REACT_APP_BACKEND_URL;
class Salesman extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Salesman: [],
      response: "",
      columns: [
        {
          dataField: "kodeLokasi",
          text: "Kode Salesman",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "namaLokasi",
          text: "Nama Salesman",
        },
        {
          dataField: "statusAktif",
          text: "Status",
          formatter: status,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeLokasi)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  data-toggle="modal"
                  data-target="#editModal"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editsalesmanList(
                        row.tipeData,
                        row.kodeLokasi,
                        row.namaLokasi,
                        row.statusAktif,
                        row.kodeExt
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getsalesmanList());
  }
  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getsalesmanList());
  };

  handleSubmit(data) {
    axios
      .post(server + "storage", {
        kodeLokasi: data.kodeSales,
        statusAktif: data.statusAktif ? data.statusAktif : "AKTIF",
        namaLokasi: data.namaSales,
        kodeExt: data.kodeExt,
        tipeData: "SALES",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getsalesmanList()))
      .then(() => this.props.dispatch(reset("Salesman")));
  }
  handleEdit(data) {
    axios
      .put(server + "storage", {
        kodeLokasi: data.kodeSales,
        statusAktif: data.statusAktif ? data.statusAktif : "AKTIF",
        namaLokasi: data.namaSales,
        kodeExt: data.kodeExt,
        tipeData: "SALES",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dirubah!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal1").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Dirubah!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getsalesmanList()));
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Data Salesman
                </h4>
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                    >
                      Tambah Data Salesman
                    </button>
                  </div>
                </div>
                {this.props.salemanList ? (
                  <ToolkitProvider
                    keyField="id"
                    data={this.props.salemanList}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group mt-5 mb-3">
                        <div className="text-right">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Pencarian"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <hr />
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          expandRow={expandRow}
                        />
                        <ExportCSVButton {...props.csvProps}>
                          <button className="btn btn-primary">
                            Export CSV
                          </button>
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img src={notrespond} style={{ width: "30%" }} />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <div class="d-flex justify-content-center">
                        <div class="spinner-border" role="status">
                          <span class="sr-only">Loading...</span>
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Salesman
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  name="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormSalesman
                  updateStatus={this.updateStatus}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Salesman
                </h4>
                <button
                  type="button"
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormSalesmanEdit
                  updateStatus={this.updateStatus}
                  onSubmit={(data) => this.handleEdit(data)}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default connect(maptoState, null)(Salesman);
