import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validationKategori from "../../Validations/validationKategori";
import axios from "axios";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const maptoState = (state) => {
  return {
    initialValues: {
      statusAktif: state.provinsi.editKategori.statusAktif,
      kodeKategori: state.provinsi.editKategori.kodeKategori,
      keterangan: state.provinsi.editKategori.keterangan,
      kodeGroup: state.provinsi.editKategori.kodeGroup,
      kodeIntern: state.provinsi.editKategori.kodeIntern,
      statusValid: state.provinsi.editKategori.statusValid,
    },
  };
};

class FormKategoriEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listKelompok: [],
    };
  }

  componentDidMount() {
    axios.get(server + "groups").then((res) =>
      this.setState({
        listKelompok: res.data,
      })
    );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 col-lg-4">
            <Field
              name="kodeKategori"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Kategori"
              readOnly={true}
              limit={5}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Kode Group</label>
            <Field
              name="kodeGroup"
              type="text"
              component="select"
              className="form-control"
              value="DEFUALT"
            >
              {this.state.listKelompok.map((list) => (
                <option value={list.kodeKelompok}>{list.kodeKelompok}</option>
              ))}
            </Field>
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="kodeIntern"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Intern"
            />
          </div>
          <div className="col-md-6 col-lg-6">
            <Field
              name="keterangan"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Keterangan"
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button
                className="btn btn-primary"
                disabled={this.props.submitting}
                type="submit"
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKategoriEdit = reduxForm({
  form: "KategoriEdit",
  validate: validationKategori,
  enableReinitialize: true,
})(FormKategoriEdit);
export default connect(maptoState, null)(FormKategoriEdit);
