import React, { Component } from "react";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptoState = (state) => {
  return {
    pembayaranSupplierList: state.provinsi.pembayaranSupplier,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};
const columns = [
  {
    dataField: "Tanggal",
    text: "Tanggal",
  },
  {
    dataField: "Supp",
    text: "Supp",
  },
  {
    dataField: "Tanggal",
    text: "No Bon",
  },
  {
    dataField: "Tanggal",
    text: "Brutto",
  },
  {
    dataField: "Tanggal",
    text: "Netto(Gr)",
  },
  {
    dataField: "Tanggal",
    text: "Rupiah",
  },
  {
    dataField: "Tanggal",
    text: "Status",
  },
];

class PembayaranSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {}
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="row">
                  <div className="col-md-3 col-lg-3">
                    <label htmlFor="">Tanggal</label>
                    <input
                      type="date"
                      name="tanggal"
                      id="tanggal"
                      className="form-control"
                    />
                  </div>
                  <div className="col-md-3 col-lg-3">
                    <label htmlFor="">Supplier</label>
                    <select
                      className="form-control"
                      id="supplier"
                      name="supplier"
                    >
                      <option value="AMG">AMG</option>
                      <option value="AYU">AYU</option>
                    </select>
                  </div>
                  <div className="col-md-3 col-lg-3">
                    <label htmlFor="">No Bon bayar</label>
                    <input
                      type="text"
                      id="bon_bayar"
                      name="bon_bayar"
                      className="form-control"
                    />
                  </div>
                  <div className="col-md-3 col-lg-3">
                    <label htmlFor="">Detail</label>
                    <textarea
                      type="text"
                      id="detail"
                      name="detail"
                      className="form-control"
                    />
                  </div>
                </div>
                <div>
                  {this.props.pembayaranSupplierList ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.props.pembayaranSupplierList}
                      columns={columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <hr />
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton {...props.csvProps}>
                            <button className="btn btn-primary">
                              Export CSV
                            </button>
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : null}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(PembayaranSupplier);
