import React, { Component } from "react";
import FormPengambilanBarcode from "./Component/FormPengambilanBarcode";

class PengambilanBarcod extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Jenis Transaksi</label>
                      <select
                        type="text"
                        id="jenis_transaksi"
                        name="jenis_transaksi"
                        className="form-control"
                      >
                        <option value="PENJUALAN">PENJUALAN</option>
                        <option value="PEMBELIAN">PEMBELIAN</option>
                      </select>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal</label>
                      <input
                        type="date"
                        id="tanggal"
                        name="tanggal"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Baki</label>
                      <input
                        type="text"
                        id="baki"
                        name="baki"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-5 col-lg-4 mt-2">
                      <label htmlFor="">Nama Toko</label>
                      <input
                        type="text"
                        id="nama_toko"
                        name="nama_toko"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-7 col-lg-4 mt-2">
                      <label htmlFor="">Detail</label>
                      <input
                        type="text"
                        id="detail"
                        name="detail"
                        className="form-control"
                      />
                    </div>
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          TAMBAH DATA
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Pengamblan Barcode
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormPengambilanBarcode />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PengambilanBarcod;
