import React, { Component } from "react";
import notrespond from "../500.svg";
import AreaProvinsiKota from "./Component/AreaProvinsiKota";
import Swal from "sweetalert2";
import axios from "axios";
import Table from "./Component/Table";
import activelogo from "../active.png";
import nonactive from "../nonactive.png";
import { connect } from "react-redux";
import {
  deleteProvinsiList,
  editProvinsiList,
  getProvinsiLIst,
} from "../actions/provinsiAction";
import Axios from "axios";
import FormProvinsiEdit from "./Component/FormProvinsiEdit";
import { reset } from "redux-form";

function status(cell, row) {
  if (row.statusAktif === "AKTIF") {
    return (
      <div>
        <img src={activelogo} style={{ width: 50, height: 50 }} />
      </div>
    );
  } else {
    return (
      <div>
        <img src={nonactive} style={{ width: 50, height: 50 }} />
      </div>
    );
  }
}

const server = process.env.REACT_APP_BACKEND_URL;
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "province", {
          data: {
            kodeProvinsi: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Gagal Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          }).then(console.log(err))
        )
        .then(() => dispatch(getProvinsiLIst()));
    }
  });
}

const maptoState = (state) => {
  return {
    provinsiList: state.provinsi.provinsi,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    status: state.provinsi.status,
  };
};

const defaultSorted = [
  {
    dataField: "kodeProvinsi",
    order: "asc",
  },
];

class Provinsi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      provinsi: [],
      provinsi_array: [],
      response: "",
      namaProvinsi: "",
      kodeProvinsi: "",
      columns: [
        {
          dataField: "kodeProvinsi",
          text: "Kode Provinsi",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "namaProvinsi",
          text: "Nama Provinsi",
          sort: true,
        },
        {
          dataField: "statusAktif",
          text: "Status",
          sort: true,
          formatter: status,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          formatter: (rowContent, row) => {
            var payload = {
              kode: row.kodeProvinsi,
              nama: row.namaProvinsi,
            };
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeProvinsi)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() =>
                    this.props.dispatch(
                      editProvinsiList(row.namaProvinsi, row.kodeProvinsi)
                    )
                  }
                >
                  <i className="fa fa-edit mr-1"></i>
                  Edit
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getProvinsiLIst());
  }

  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getProvinsiLIst());
  };

  edit = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  handleSubmit(data) {
    axios
      .post(server + "province", {
        statusAktif: data.status ? data.status : "AKTIF",
        namaProvinsi: data.NamaProvinsi,
        kodeProvinsi: data.KodeProvinsi,
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        }).then(console.log(err))
      )
      .then(() => this.props.dispatch(getProvinsiLIst()))
      .then(() => this.props.dispatch(reset("Provinsi")));
  }
  handleEdit(data) {
    axios
      .put(server + "province", {
        statusAktif: data.status ? data.status : "AKTIF",
        namaProvinsi: data.NamaProvinsi,
        kodeProvinsi: data.KodeProvinsi,
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal1").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        }).then(console.log(err))
      )
      .then(() => this.props.dispatch(getProvinsiLIst()));
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Data Provinsi
                </h4>
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                    >
                      Tambah Data Provinsi
                    </button>
                  </div>
                </div>
                <div className="col-lg-12 col-md-12 mt-5">
                  {this.props.provinsiList ? (
                    <Table
                      columns={this.state.columns}
                      data={this.props.provinsiList}
                      defaultSorted={defaultSorted}
                    />
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <div class="d-flex justify-content-center">
                          <div class="spinner-border" role="status">
                            <span class="sr-only">Loading...</span>
                          </div>
                        </div>
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Provinsi
                </h4>
                <button
                  id="dismiss_modal"
                  name="dismiss_modal"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <AreaProvinsiKota
                  nama="Nama Provinsi"
                  isiNama={this.state.namaProvinsi}
                  kode="Kode Provinsi"
                  isikode={this.state.kodeProvinsi}
                  from="provinsi"
                  edit={this.edit}
                  updateStatus={this.updateStatus}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Provinsi
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormProvinsiEdit
                  nama="Nama Provinsi"
                  kode="Kode Provinsi"
                  from="provinsi"
                  edit={this.edit}
                  updateStatus={this.updateStatus}
                  onSubmit={(data) => this.handleEdit(data)}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Provinsi);
