import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import axios from "axios";
import Kadar_kadar_perhiasan from "./Component/Kadar_kadar_perhiasan";
import Kadar_pembelian from "./Component/Kadar_pembelian";
import Kadar_penjualan from "./Component/Kadar_penjualan";
import { connect } from "react-redux";
import { editKadar, getKadarList } from "../actions/kadarAction";
import Swal from "sweetalert2";
import notrespond from "../500.svg";
import activelogo from "../active.png";
import nonactive from "../nonactive.png";
import Kadar_kadar_perhiasanEdit from "./Component/Kadar_kadar_perhiasanEdit";
import { reset } from "redux-form";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function status(cell, row) {
  if (row.statusAktif === "AKTIF") {
    return (
      <div>
        <img src={activelogo} style={{ width: 50, height: 50 }} alt="ACTIVE" />
      </div>
    );
  } else {
    return (
      <div>
        <img
          src={nonactive}
          alt="NON ACTIVE"
          style={{ width: 50, height: 50 }}
        />
      </div>
    );
  }
}
const maptoState = (state) => {
  return {
    kadarList: state.provinsi.kadar,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};
function hapus(dispatch, kode, tipe) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "rate", {
          data: {
            kadar: kode,
            tipeKadar: tipe,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Hapus Data Gagal",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .then(() => dispatch(getKadarList(tipe)));
    }
  });
}
const server = process.env.REACT_APP_BACKEND_URL;

class Kadar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Kadar: [],
      response: "",
      tipe: "KADAR-CETAK",
      columns: [
        {
          dataField: "kadar",
          text: "Kadar",
        },
        {
          dataField: "tipeKadar",
          text: "Tipe Kadar",
        },
        {
          dataField: "statusAktif",
          text: "Status",
          sort: true,
          formatter: status,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    hapus(this.props.dispatch, row.kadar, row.tipeKadar)
                  }
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                {/* <button
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() =>
                    this.props.dispatch(
                      editKadar(row.kadar, row.tipeKadar, row.statusAktif)
                    )
                  }
                >
                  <i className="fa fa-edit mr-1"></i>
                  EDIT
                </button> */}
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getKadarList("KADAR-CETAK"));
  }

  changeTable = (input) => (e) => {
    this.props.dispatch(getKadarList(e.target.value));
  };

  handleSubmit_perhiasan(data) {
    axios
      .post(server + "rate", {
        kadar: data.kadar,
        tipeKadar: "KADAR-CETAK",
        statusAktif: data.status ? data.status : "AKTIF",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getKadarList("KADAR-CETAK")))
      .then(() => this.props.dispatch(reset("kadarPerhiasan")));
  }
  handleSubmit_perhiasan_edit(data) {
    axios
      .put(server + "rate", {
        kadar: data.kadar,
        tipeKadar: "KADAR-CETAK",
        statusAktif: data.status ? data.status : "AKTIF",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getKadarList("KADAR-CETAK")));
  }

  handleSubmit_pembelian(data) {
    axios
      .post(server + "rate", {
        kadar: data.kadar,
        tipeKadar: "KADAR-BELI",
        statusAktif: data.status ? data.status : "AKTIF",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getKadarList("KADAR-BELI")))
      .then(() => this.props.dispatch(reset("kadarPembelian")));
  }
  handleSubmit_pembelian_edit(data) {
    axios
      .put(server + "rate", {
        kadar: data.kadar,
        tipeKadar: "KADAR-BELI",
        statusAktif: data.status ? data.status : "AKTIF",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getKadarList("KADAR-BELI")));
  }

  handleSubmit_penjualan(data) {
    axios
      .post(server + "rate", {
        kadar: data.kadar,
        tipeKadar: "KADAR-JUAL",
        statusAktif: data.status ? data.status : "AKTIF",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getKadarList("KADAR-JUAL")))
      .then(() => this.props.dispatch(reset("kadarPenjualan")));
  }
  handleSubmit_penjualan_edit(data) {
    axios
      .put(server + "rate", {
        kadar: data.kadar,
        tipeKadar: "KADAR-JUAL",
        statusAktif: data.status ? data.status : "AKTIF",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getKadarList("KADAR-JUAL")));
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-4" style={{ fontSize: 30 }}>
                  Data Kadar
                </h4>
                <form action="#" className="mb-5">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          type="button"
                          className="btn btn-info"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          Tambah Data Kadar
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                {this.props.kadarList ? (
                  <ToolkitProvider
                    keyField="id"
                    data={this.props.kadarList}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <div className="row">
                          <div className=" col-md-3 col-lg-3 text-left mb-3">
                            <select
                              className="form-control"
                              onChange={this.changeTable()}
                            >
                              <option value="KADAR-CETAK">
                                KADAR PERHIASAN
                              </option>
                              <option value="KADAR-BELI">KADAR BELI</option>
                              <option value="KADAR-JUAL">KADAR JUAL</option>
                            </select>
                          </div>
                          <div className=" col-md-9 col-lg-9 text-right mb-3">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="PERNCARIAN"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                        />
                        <ExportCSVButton {...props.csvProps}>
                          <button className="btn btn-primary">
                            Export CSV
                          </button>
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img
                          src={notrespond}
                          style={{ width: "30%" }}
                          alt="NOT RESPOND"
                        />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <div class="d-flex justify-content-center">
                        <div class="spinner-border" role="status">
                          <span class="sr-only">Loading...</span>
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Kadar
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  name="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <div className="col-lg-12 col-md-12">
                  <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                    <li className="nav-item">
                      <a
                        href="#hutang-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link"
                      >
                        <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">
                          Kadar Perhiasan
                        </span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#stock-tab"
                        data-toggle="tab"
                        aria-expanded="true"
                        className="nav-link active"
                      >
                        <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Harga Beli</span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#piutang-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link"
                      >
                        <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Harga Jual</span>
                      </a>
                    </li>
                  </ul>
                  <div className="tab-content">
                    <div className="tab-pane" id="hutang-tab">
                      <Kadar_kadar_perhiasan
                        onSubmit={(data) => this.handleSubmit_perhiasan(data)}
                      />
                    </div>
                    <div className="tab-pane show active" id="stock-tab">
                      <Kadar_pembelian
                        onSubmit={(data) => this.handleSubmit_pembelian(data)}
                      />
                    </div>
                    <div className="tab-pane" id="piutang-tab">
                      <Kadar_penjualan
                        onSubmit={(data) => this.handleSubmit_penjualan(data)}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Kadar
                </h4>
                <button
                  type="button"
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <div className="col-lg-12 col-md-12">
                  <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                    <li className="nav-item">
                      <a
                        href="#hutang-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link"
                      >
                        <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">
                          Kadar Perhiasan
                        </span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#stock-tab"
                        data-toggle="tab"
                        aria-expanded="true"
                        className="nav-link active"
                      >
                        <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Harga Beli</span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#piutang-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link"
                      >
                        <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Harga Jual</span>
                      </a>
                    </li>
                  </ul>
                  <div className="tab-content">
                    <div className="tab-pane" id="hutang-tab">
                      <Kadar_kadar_perhiasanEdit
                        onSubmit={(data) =>
                          this.handleSubmit_perhiasan_edit(data)
                        }
                      />
                    </div>
                    <div className="tab-pane show active" id="stock-tab">
                      <Kadar_pembelian
                        onSubmit={(data) =>
                          this.handleSubmit_pembelian_edit(data)
                        }
                      />
                    </div>
                    <div className="tab-pane" id="piutang-tab">
                      <Kadar_penjualan
                        onSubmit={(data) =>
                          this.handleSubmit_penjualan_edit(data)
                        }
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Kadar);
