import React, { Component } from "react";
import FormRekening from "./Component/FormRekening";
import Table from "./Component/Table";
import axios from "axios";
import { connect } from "react-redux";
import notrespond from "../500.svg";
import { editRekening, getrekeningList } from "../actions/rekeningAction";
import Swal from "sweetalert2";
import FormRekeningEdit from "./Component/FormRekeningEdit";
import { reset } from "redux-form";

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "bank", {
          data: {
            noRekening: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Data Gagal Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .then(() => dispatch(getrekeningList()));
    }
  });
}

const defaultSorted = [
  {
    dataField: "nomorRekening",
    order: "asc",
  },
];
const maptoState = (state) => {
  return {
    daftarList: state.provinsi.rekening,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

const server = process.env.REACT_APP_BACKEND_URL;

class DaftarRekening extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Rekening: [],
      response: "",
      columns: [
        {
          dataField: "noRekening",
          text: "Nomor Rekening",
        },
        {
          dataField: "namaBank",
          text: "Nama Bank",
        },
        {
          dataField: "atasNama",
          text: "Atas Nama",
        },
        {
          dataField: "mataUang",
          text: "Mata Uang",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.noRekening)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() =>
                    this.props.dispatch(
                      editRekening(
                        row.noRekening,
                        row.atasNama,
                        row.namaBank,
                        row.mataUang
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getrekeningList());
  }

  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getrekeningList());
  };

  handleSubmit(data) {
    axios
      .post(server + "bank", {
        statusAktif: "AKTIF",
        atasNama: data.atasNama,
        namaBank: data.namaBank,
        noRekening: data.noRekening,
        mataUang: data.mataUang ? data.mataUang : "",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getrekeningList()))
      .then(() => this.props.dispatch(reset("DataRekening")));
  }

  handleEdit(data) {
    axios
      .put(server + "bank", {
        statusAktif: "AKTIF",
        atasNama: data.atasNama,
        namaBank: data.namaBank,
        noRekening: data.noRekening,
        mataUang: !data.mataUang === "" ? data.mataUang : "",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Diubah!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal1").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Diubah!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getrekeningList()));
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Data Rekening
                </h4>
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                      // data-target="#editModal"
                    >
                      Tambah Data Rekening
                    </button>
                  </div>
                </div>
                {this.props.daftarList ? (
                  <Table
                    data={this.props.daftarList}
                    columns={this.state.columns}
                    defaultSorted={defaultSorted}
                  />
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img src={notrespond} style={{ width: "30%" }} />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <div className="d-flex justify-content-center">
                        <div className="spinner-border" role="status">
                          <span className="sr-only">Loading...</span>
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Rekening
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  nama="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormRekening
                  updateStatus={this.updateStatus}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Rekening
                </h4>
                <button
                  type="button"
                  id="dismiss_modal1"
                  nama="dismiss_modal1"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormRekeningEdit
                  updateStatus={this.updateStatus}
                  onSubmit={(data) => this.handleEdit(data)}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(DaftarRekening);
