import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import axios from "axios";
import paginationFactory from "react-bootstrap-table2-paginator";
import activelogo from "../active.png";
import nonactive from "../nonactive.png";
import { connect } from "react-redux";
import { editkategoriList, getkategoriList } from "../actions/kategoriAction";
import notrespond from "../500.svg";
import Swal from "sweetalert2";
import FormKategori from "./Component/FormKategori";
import FormKategoriEdit from "./Component/FormKategoriEdit";
import { Link } from "react-router-dom";
import { reset } from "redux-form";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
const server = process.env.REACT_APP_BACKEND_URL;
function status(cell, row) {
  if (row.statusAktif === "AKTIF") {
    return (
      <div>
        <img src={activelogo} style={{ width: 50, height: 50 }} />
      </div>
    );
  } else {
    return (
      <div>
        <img src={nonactive} />
      </div>
    );
  }
}
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "category", {
          data: {
            kodeKategori: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Data Gagal Ditambahkan!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .then(() => dispatch(getkategoriList()));
    }
  });
}

const maptoState = (state) => {
  return {
    kategoriList: state.provinsi.kategori,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

class Kategori extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kodeKategori",
          text: "Kode Kategori",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "kodeGroup",
          text: "Kode Group",
        },
        {
          dataField: "statusAktif",
          text: "Status",
          formatter: status,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "link",
          text: "Aksi",
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeKategori)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  data-toggle="modal"
                  data-target="#editModal"
                  className="btn btn-warning mr-2"
                  onClick={() =>
                    this.props.dispatch(
                      editkategoriList(
                        row.statusAktif,
                        row.kodeKategori,
                        row.keterangan,
                        row.kodeGroup,
                        row.kodeIntern,
                        row.statusValid
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-1"></i>
                  Edit
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getkategoriList());
  }

  handleSubmit(data) {
    axios
      .post(server + "category", {
        statusAktif: data.status ? data.status : "AKTIF",
        kodeKategori: data.kodeKategori,
        keterangan: data.keterangan,
        kodeGroup: data.kodeGroup,
        kodeIntern: data.kodeIntern,
        statusValid: data.statusValid ? data.statusValid : "VALID",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getkategoriList()))
      .then(() => this.props.dispatch(reset("Kategori")));
  }
  handleEdit(data) {
    axios
      .put(server + "category", {
        statusAktif: data.statusAktif ? data.statusAktif : "AKTIF",
        kodeKategori: data.kodeKategori,
        keterangan: data.keterangan,
        kodeGroup: data.kodeGroup,
        kodeIntern: data.kodeIntern,
        statusValid: data.statusValid ? data.statusValid : "VALID",
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal1").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getkategoriList()));
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <ol className="breadcrumb mb-2">
          <Link to="/ayugold.com/KlasifikasiBarang">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-md-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-4" style={{ fontSize: 30 }}>
                  Kategori
                </h4>
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                    >
                      Tambah Data Kategori
                    </button>
                  </div>
                </div>
                {this.props.kategoriList ? (
                  <ToolkitProvider
                    keyField="id"
                    data={this.props.kategoriList}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group mt-5 mb-3">
                        <div className="text-right">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Pencarian"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          pagination={paginationFactory()}
                        />
                        <ExportCSVButton {...props.csvProps}>
                          <button className="btn btn-primary">
                            Export CSV
                          </button>
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img src={notrespond} style={{ width: "30%" }} />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <div className="d-flex justify-content-center">
                        <div className="spinner-border" role="status">
                          <span className="sr-only">Loading...</span>
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Kategori
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormKategori onSubmit={(data) => this.handleSubmit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Kategori
                </h4>
                <button
                  type="button"
                  id="dismiss_modal1"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormKategoriEdit onSubmit={(data) => this.handleEdit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Kategori);
