const validationRosok = (value) => {
    const errors = {}
    if (!value.kodeKategori) {
        errors.kodeKategori = "Tidak Boleh kosong"
    } else if (value === "DEFAULT"){
        errors.kodeKategori = "Pilih Terlebih dahulu";
    }
    return errors
}

export default validationRosok;