import React, { Component } from "react";

class FormPengambilanBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      DataSupplier: [],
      kode_barang: "",
      keterangan: "",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    var data = {
      kode_barang: this.state.kode_barang,
      keterangan: this.state.keterangan,
      kadar: this.state.kadar,
      P: this.state.P,
      K: this.state.K,
      bungkus: this.state.bungkus,
      berat: this.state.berat,
      harga: this.state.harga,
      murni: this.state.murni,
      ongkos: this.state.ongkos,
    };
    var array = this.state.DataSupplier;
    array.push(data);
    console.log(JSON.stringify(this.state.DataSupplier));
    this.setState({ status: "sukses" });
  };
  render() {
    return (
      <div className="row">
        <div className="col-md-4 form-group">
          <label htmlFor="">Kode Barang</label>
          <select
            name="kode_barang"
            id="kode_barang"
            className="form-control"
            onChange={this.inputChange("kode_barang")}
          >
            <option value="BARANG 1">BARANG 1</option>
            <option value="BARANG 2">BARANG 2</option>
          </select>
        </div>
        <div className="col-md-8 form-group">
          <label htmlFor="">Keterangan</label>
          <input
            name="keterangan"
            id="keterangan"
            className="form-control"
            onChange={this.inputChange("keterangan")}
          />
        </div>
        <div className="custom-control custom-checkbox col-md-3">
          <input
            type="checkbox"
            className="custom-control-input"
            id="customCheck2"
            checked
          />
        </div>
        <div className="col-md-3 form-group">
          <label htmlFor="">Barcode</label>
          <input
            name="barcode"
            id="barcode"
            className="form-control"
            onChange={this.inputChange("P")}
          />
        </div>
        <div className="col-md-3 form-group">
          <label htmlFor="">Bungkus(Gr)</label>
          <input
            name="bungkus"
            id="bungkus"
            className="form-control"
            onChange={this.inputChange("K")}
          />
        </div>
        <div className="col-md-3 form-group">
          <label htmlFor="">Berat + Bungkus (Gr)</label>
          <input
            name="berat_bungkus"
            id="berat_bungkus"
            className="form-control"
            onChange={this.inputChange("bungkus")}
          />
        </div>
        <div className="col-md-3 form-group">
          <label htmlFor="">Berat(Gr)</label>
          <input
            name="Berat"
            id="Berat"
            className="form-control"
            onChange={this.inputChange("berat")}
          />
        </div>
        <div className="col-md-3 form-group">
          <label htmlFor="">Kadar</label>
          <input
            name="kadar"
            id="kadar"
            className="form-control"
            onChange={this.inputChange("harga")}
          />
        </div>
        <div className="col-md-3 form-group">
          <label htmlFor="">Harga</label>
          <input
            name="Harga"
            id="Harga"
            className="form-control"
            onChange={this.inputChange("harga")}
          />
        </div>
        <div className="col-md-3 form-group">
          <label htmlFor="">Murni(Gr)</label>
          <input
            name="Murni"
            id="Murni"
            className="form-control"
            onChange={this.inputChange("murni")}
          />
        </div>
        <div className="col-md-3 form-group">
          <label htmlFor="">Ongkos(Rp)</label>
          <input
            name="Ongkos"
            id="Ongkos"
            className="form-control"
            onChange={this.inputChange("ongkos")}
          />
        </div>
        <div className="col-md-12 form-group mt-3">
          <div className="text-right">
            <button className="btn btn-primary" onClick={this.submit}>
              SIMPAN
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default FormPengambilanBarcode;
