import React, { Component } from "react";
import kodeBarang from "../kode_barang.svg";
import kelompok from "../kelompok.svg";
import kategori from "../kategori.svg";
import { Link } from "react-router-dom";

class KlasifikasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-fluid" style={{ width: "100%" }}>
        <div className="row col-lg-12">
          <div className="col-lg-4">
            <div className="card">
              <div className="card-body text-center">
                <img src={kategori} width="300" alt="Kategori" />
                <h3>Kategori</h3>
                <Link to="/ayugold.com/kategori">
                  <button className="btn btn-primary">Masuk</button>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-lg-4">
            <div className="card">
              <div className="card-body text-center">
                <img src={kodeBarang} width="300" alt="Kode Barang" />
                <h3>Kode Barang</h3>
                <Link to="/ayugold.com/kodebarang">
                  <button className="btn btn-primary">Masuk</button>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-lg-4">
            <div className="card">
              <div className="card-body text-center">
                <img src={kelompok} width="300" alt="Kode Barang" />
                <h3>Kelompok</h3>
                <Link to="/ayugold.com/kelompok">
                  <button className="btn btn-primary">Masuk</button>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default KlasifikasiBarang;
