import React, { Component } from "react";
import FormTerimaSupplier from "./Component/FormTerimaSupplier";
import { connect } from "react-redux";
import { getsupplierList } from "../actions/supplierAction";
import Axios from "axios";
import BootstrapTable from "react-bootstrap-table-next";
import PDF from "./Component/PDF";

const ref = React.createRef();
const server = process.env.REACT_APP_BACKEND_URL;
const maptostate = (state) => {
  return {
    supplierList: state.provinsi.supplier,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

class TerimaSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "kodeBarang",
          text: "Kode Barang",
        },
        {
          dataField: "kadar",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "p",
          text: "P",
        },
        {
          dataField: "k",
          text: "K",
        },
        {
          dataField: "bungkus",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "berat",
          text: "Berat(Gr)",
        },
        {
          dataField: "harga",
          text: "Harga(%)",
        },
        {
          dataField: "murni",
          text: "Murni(Gr)",
        },
        {
          dataField: "ongkosKirim",
          text: "Ongkos(RP)",
        },
      ],
      title: "PENERIMAAN SUPPLIER",
      content: "HELLO",
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => alert(err));
  }

  handleSubmit(data) {
    console.log(data);
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <PDF
              title={this.state.title}
              content={this.state.content}
              image={this.state.image}
            />
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Supplier</label>
                      <select
                        name="supplier"
                        id="supplier"
                        className="form-control"
                      >
                        {this.state.listSupplier.map((kode) => (
                          <option value={kode.kodeSupplier}>
                            {kode.namaSupplier}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal Invoice</label>
                      <input
                        type="date"
                        id="tanggal_invoice"
                        name="tanggal_invoice"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal Barang</label>
                      <input
                        type="date"
                        id="tanggal_invoice"
                        name="tanggal_invoice"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-6 col-lg-4 mt-3">
                      <label htmlFor="">Keterangan</label>
                      <input
                        type="text"
                        id="keterangan"
                        name="keterangan"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-4 col-lg-4 mt-3">
                      <label htmlFor="">No Bon</label>
                      <input
                        type="text"
                        id="keterangan"
                        name="keterangan"
                        className="form-control"
                      />
                    </div>
                    <div className="col-md-12 col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          Tambah Data
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabindex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Terima Supplier
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormTerimaSupplier
                  from="terima"
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(TerimaSupplier);
